
.chat-detail {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  width: 100%;
  line-height: 1.5;
  text-align: left;
  font-size: 14px;
  color: #333;
}
.chat-info {
  display: none;
  margin: 12px;
  padding: 12px;
  background-color: #f2f3f7;
  border-radius: 5px;
}
.chat-info .chat-title {
    margin-top: 0;
    margin-bottom: 10px;
    font-weight: normal;
    font-size: 16px;
}
.chat-info p {
    margin: 0;
}
.chat-info .chat-author {
    margin-top: 5px;
}
.chat-list {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 50px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  margin: 12px 12px 0;
}
.chat-list p {
    margin: 0;
}
.chat-list-item {
    margin-bottom: 15px;
}
.chat-list-row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.chat-list-row .chat-avatar {
      text-align: center;
      font-size: 12px;
}
.chat-list-row .chat-avatar span {
        display: inline-block;
        margin-bottom: 3px;
        width: 21px;
        height: 21px;
        line-height: 21px;
        background-color: #eae9ef;
        border-radius: 50%;
}
.chat-list-row .chat-list-content {
      margin: 0;
      padding: 12px;
      background-color: #f3eee8;
      border-radius: 10px;
}
.chat-list-row.main {
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
}
.chat-list-row.main .chat-list-content {
        background-color: #f3eee8;
        border-bottom-left-radius: 0;
}
.chat-list-row.left {
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
}
.chat-list-row.left .chat-avatar {
        margin-right: 10px;
}
.chat-list-row.left .chat-list-content {
        background-color: #eae9ef;
        border-bottom-left-radius: 0;
}
.chat-list-row.right {
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
}
.chat-list-row.right .chat-avatar {
        margin-left: 10px;
}
.chat-list-row.right .chat-avatar span {
          background-color: #0290fe;
}
.chat-list-row.right .chat-list-content {
        background-color: #0290fe;
        border-bottom-right-radius: 0;
}
.chat-list .chat-item-reply {
    position: relative;
    margin-top: 10px;
    padding: 3px 12px;
    background-color: #f3eee8;
    border-radius: 5px;
    font-size: 12px;
    color: #999;
}
.chat-list .chat-item-reply:before {
      content: '';
      position: absolute;
      left: 10px;
      top: -5px;
      z-index: -1;
      width: 10px;
      height: 10px;
      background-color: #f3eee8;
      border: 1px solid #f3eee8;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.chat-list .chat-item-reply.right:before {
      left: auto;
      right: 10px;
}
.chat-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 50px;
  background-color: #f2f3f7;
}
.chat-footer button {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background-color: transparent;
    border: 0;
    color: #666;
    font-size: 16px;
    -webkit-appearance: none;
    outline: 0;
}
.chat-footer button + button {
      border-left: 1px solid #ccc;
}
.chat-footer button:focus {
      outline: 0;
}
.chat-dialog {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  display: none;
  background-color: #f2f3f7;
}
.chat-dialog .chat-comment-form {
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -105px 0 0 -125px;
    width: 250px;
    height: 210px;
}
.chat-dialog .chat-comment-form h3 {
      margin: 0 0 20px;
      font-weight: normal;
      font-size: 16px;
}
.chat-dialog .chat-comment-form .chat-cf-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin-bottom: 20px;
}
.chat-dialog .chat-comment-form .chat-cf-item:last-child {
        margin-bottom: 0;
}
.chat-dialog .chat-comment-form input, .chat-dialog .chat-comment-form textarea {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 3px;
      line-height: 1.5;
      border: 0;
      border-radius: 3px;
      font-size: 14px;
      color: #666;
}
.chat-dialog .chat-comment-form input:focus, .chat-dialog .chat-comment-form textarea:focus {
        outline: 0;
}
.chat-dialog .chat-comment-form textarea {
      height: 63px;
      resize: none;
}
.chat-dialog .chat-comment-form button {
      padding: 6px 15px;
      background-color: #0290fe;
      border: 0;
      border-radius: 3px;
      -webkit-appearance: none;
      font-size: 16px;
      color: #fff;
}
.chat-dialog .chat-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    text-decoration: none;
    color: #333;
}



